/* add css module styles here (optional) */

._styles-module__test__3ybTi {
  margin: 2em;
  padding: 0.5em;
  border: 2px solid #000;
  font-size: 2em;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .pagination-list-search {
    display: grid;
    grid-template-columns: 1fr;
  }

  .pagination-space-evenly {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin: 0.5rem 1rem;
  }
  .pagination-space-evenly:nth-child(2) {
    flex-direction: row-reverse;
    justify-content: flex-end;
  }
  .pagination-space-evenly > input {
    width: 50%;
    margin-right: 1rem;
  }
}
@media screen and (min-width: 767px) {
  .pagination-list-search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: black;
    margin-bottom: 1rem;
  }

  .pagination-space-evenly {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    align-items: center;
    justify-content: center;
  }
}

.pagination {
  color: black;
}

.pagination-results {
  color: black;
  text-align: center;
}
.pagination-list-search label {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  margin: 0;
}
